import { mapState, mapActions, mapMutations } from 'vuex';
export default {
	computed: mapState(['userId']),
	data() {
		return {
			imgUrl: '',
			jsonData: ''
		};
	},
	methods: {
		selectImage() {
			let that = this;
			uni.chooseImage({
				success: chooseImageRes => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: 'http://www.shenzepengzuishuai.cn/sso-file-service-0.0.1-SNAPSHOT/uploadImg',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test'
						},
						success: uploadFileRes => {
							that.jsonData = uploadFileRes;
						}
					});
				}
			});
		},
		button() {
			console.log(this.jsonData.data);
			let objData = JSON.parse(this.jsonData.data);
			console.log(objData);
			this.imgUrl = objData.data.imgUrl;
			console.log(objData.data.imgUrl);
			let that = this;
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/update/user/imgUrl',
				method: 'PUT',
				data: {
					id: that.userId,
					imgUrl: that.imgUrl
				},
				success: res => {
					uni.showToast({
						title: '上传成功'
					});
				},
				fail: () => {},
				complete: () => {
					uni.hideToast();
				}
			});
		}
	}
};
